.calculator-select {
    position: relative;
    &.calculator-is-open {
        .calculator-select__body { display: block; }
    }
    &__header {
        cursor: pointer;
    }
    &__body {
        display: none;
        left: 0;
        position: absolute;
        top: 100%;
        right: 0;
    }
    &__item {
        cursor: pointer;
    }

    &__current {
        font-style: normal;
        font-weight: 500;
        font-size: 1.13vw;
        line-height: 1.13vw;
        color: var(--main-color);
        position: relative;

        &::before {
            content: "";
            position: absolute;
            border: 0.19vw solid transparent;
            border-top: 0.19vw solid var(--main-color);
            right: -0.6vw;
            top: 0.5vw;
        }
    }
}

@media (max-width: $mobile-width) {
    .calculator-select__current {
        font-size: 16px;
        line-height: 18px;

        &::before {
            border: 4px solid transparent;
            border-top: 4px solid #000;
            right: -10px;
            top: 7px;
        }
    }
}