.calculator {
    &__container {
        padding-left: $psize;
        padding-right: $psize;

        h1 {
            font-style: normal;
            font-weight: 500;
            font-size: $f40;
            line-height: 3.7vw;
            padding-bottom: $f40;
        }
    }
    &__main-title {
        display: flex;
        justify-content: space-between;
        padding-bottom: 0.6vw;

        p {
            width: 50%;
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 22px;
        }
    }
    &__select-item {
        width: 50%;

        &:first-child {
            margin-right: $f20;
        }

        p {
            width: 50%;
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 22px;
            padding-bottom: 0.6vw;
        }

        .calculator-select {
            position: relative;
            &.calculator-is-open {
                .select__body {
                    display: block;
                }
            }
            &__header {
                cursor: pointer;
                border: 1px solid var(--border);
                padding: 1.1vw 1.4vw;

                &::before {
                    content: "";
                    position: absolute;
                    border: 0.19vw solid transparent;
                    border-top: 0.19vw solid var(--main-color);
                    right: 2vw;
                    top: 2vw;
                }
            }
            &__body {
                display: none;
                left: 0;
                position: absolute;
                top: 100%;
                right: 0;
                z-index: 1;
                height: 270px;
                overflow-y: scroll;
                border-bottom: 1px solid var(--border);
            }
            &__item {
                cursor: pointer;
                border-left: 1px solid var(--border);
                border-right: 1px solid var(--border);
                padding: 1.1vw 1.4vw;
                background: #fff;

                &:hover {
                    background: #FCFCFC;
                }
            }

            &__current {
                width: 100%;

                &::before {
                    display: none;
                }
            }
        }
    }
    &__main-select {
        display: flex;
        width: 100%;
        justify-content: space-between;
        padding-bottom: $f30;
        padding-right: $f40;
    }
    &__main {
        padding-top: 1.6vw;
        border-top: 4px solid #F57400;
        background: #FFFFFF;
        box-shadow: 0px 0px 96px rgba(0, 0, 0, 0.1);
        padding-left: 2.8vw;
        margin-bottom: 5.55vw;
    }
    &__main-block {
        display: flex;
        justify-content: space-between;
    }
    &__product-item {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-bottom: 0.94vw;
        padding-top: 0.94vw;
        border-bottom: 1px solid var(--border);
        padding-right: $f40;

        &:hover {
            background: #FCFCFC;
            cursor: pointer;
        }

        .calculator-checkbox {
            cursor: pointer;
            display: block;
            padding-left: 35px;
            position: relative;
            user-select: none;
            input {
                cursor: pointer;
                opacity: 0;
                position: absolute;
                &:checked {
                    & ~ .checkbox__custom {
                        background-color: #fff;
                        &:after {
                            display: block;
                        }
                    }
                }
            }
            &__custom {
                background-color: #ffffff;
                border: 1px solid #000000;
                border-radius: 50%;
                left: 0;
                height: $f28;
                position: absolute;
                top: -0.9vw;
                right: 0;
                width: $f28;
                &:after {
                    content: '';
                    display: none;
                    height: 1.1vw;
                    top: 0.3vw;
                    position: absolute;
                    border-radius: 50%;
                    background-color:  #F57400;
                    width: 1.1vw;
                }
            }
        }

        p {
            font-style: normal;
            font-weight: 500;
            line-height: $f20;
        }

        img {
            width: $f40;
            height: $f40;
            margin-right: 1.6vw;
        }
    }
    &__product-list {
        width: 100%;
        max-width: 24.2vw;
        max-height: 48.8vw;
        overflow-y: scroll;

        &::-webkit-scrollbar {
            width: 0.4vw;
            background: linear-gradient(0deg, #E3E3E3, #E3E3E3), #E3E3E3;
            height: 10vw;
            max-height: 55px;
        }

        &::-webkit-scrollbar-thumb {
            background-color: #919191;
        }

        &::-webkit-scrollbar-track {
            height: 55px;
        }
    }
    &__item-wrapper {
        display: flex;
        align-items: center;
    }
    &__option-block {
        width: 100%;
        height: 100%;

        .calculator-tabs-content {
            &__calc-item {
                display: none;
                &.calculator-is-active { display: flex; }
            }
        }
    }
    &__option-title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-left: $f40;
        padding-right: $f40;

        h2 {
            font-style: normal;
            font-weight: 500;
            font-size: $f24;
            line-height: $f40;
        }
    }
    &__tabs-nav {
        display: flex;
        position: relative;

        li {
            cursor: pointer;
            padding-right: 0.63vw;
            padding-left: 0.63vw;
            font-style: normal;
            font-weight: 600;
            line-height: $f20;
            padding-bottom: $f28;
            position: relative;
            border-bottom: 2px solid #80889E;

            &:first-child {
                padding-left: 0;
            }

            &:last-child {
                padding-right: 0;
            }
        }
    }
    &__main-text {
        padding-left: $psize;
        padding-right: $psize;
        padding-top: 3.7vw;
        border-top: 1px solid var(--border);
        padding-bottom: 7.5vw;

        p {
            font-style: normal;
            font-weight: normal;
            font-size: 1.4vw;
            line-height: 2vw;
            padding-bottom: $f30;

            &:last-child {
                padding-bottom: 0;
            }
        }
    }
    &__select-item-mob {
        display: none;
    }
    &__sub-btn {
        font-style: normal;
        font-weight: 600;
        font-size: 1.35vw;
        line-height: 1.75vw;
        color: var(--alt-color);
        padding: 17.5px 30px;
        border-radius: 80px;
        background-color: #F57400;
        width: 100%;
        margin-top: $f30;
        cursor: pointer;
    }

    &-tabs {
        &-content.calculator__tabs-content {
            padding-left: 0!important;
            padding-right: 0;
            max-height: 100%;
            height: 100%;
        }
        &-content.calculator__tabs-content {
            padding-left: $f40;
        }
        &-nav__calc-item.calculator-is-active {
            color: #F57400;
            border-bottom: 2px solid #F57400;
            position: relative;
        }
        &-content {
            &__calc-item {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                height: 100%;

                img {
                    width: 17.81vw;
                    height: 17.81vw;
                    margin-top: $f30;
                }
            }
            &__input {
                position: relative;
                padding-bottom: 0.9vw;

                &:last-child {
                    padding-bottom: 0;
                }

                label {
                    position: relative;
                    font-style: normal;
                    font-weight: 500;
                    font-size: 1vw;
                    line-height: $f24;
                    padding-bottom: 0.1vw;
                }

                p {
                    position: absolute;
                    font-style: normal;
                    font-weight: 500;
                    font-size: 1vw;
                    line-height: $f24;
                    z-index: 100;
                    right: 1vw;
                    top: 2.5vw;
                }

                input {
                    width: 100%;
                    padding: 0.9vw;
                    background: #FCFCFC;
                    border: 1px solid #E0E0E0;
                    box-sizing: border-box;
                    border-radius: 1px;
                    position: relative;

                    &::-webkit-outer-spin-button {
                        -webkit-appearance: none;
                    }

                    &::-webkit-inner-spin-button {
                        -webkit-appearance: none;
                    }

                    &::placeholder {
                        font-style: normal;
                        font-weight: 500;
                        font-size: 1vw;
                        line-height: $f24;
                        position: relative;
                        color: var(--main-color);
                        right: 0;
                    }
                }
            }
            &__calc-inputs {
                flex-direction: column;
                display: flex;
                position: relative;

            }
            &__calc-column {
                width: 100%;
                max-width: 21.6vw;
                padding-top: $f40;
                height: 100%;
            }
            &__item-footer {
                padding-left: $f40;
                padding-right: $f40;
                background-color: var(--grey-bg);
                margin-top: 13.8vw;
            }
            &__footer-block {
                display: flex;
                flex-direction: column;
                justify-content: flex-end;
                align-items: flex-end;
                padding-top: $f20;
                padding-bottom: $f20;

                p {
                    font-style: normal;
                    font-weight: 500;
                    font-size: $f20;
                    line-height: 1.4vw;
                }

                h3 {
                    font-style: normal;
                    font-weight: 500;
                    font-size: $f34;
                    line-height: $f40;
                    color: #F57400;
                }
            }
            &__item-wrapper {
                display: flex;
                justify-content: space-between;
                padding-left: $f40;
                padding-right: $f40;
            }
        }
    }
}

@media (max-width: $mobile-width) {
    .calculator {
        &__container {
            padding-right: 20px;
            padding-left: 20px;

            h1 {
                font-style: normal;
                font-weight: 600;
                font-size: 40px;
                line-height: 44px;
                padding-top: 53px;
                padding-bottom: 40px;
            }
        }
        &__main-select {
            flex-direction: column;
            padding-right: 12px;
            padding-left: 12px;
            padding-bottom: 26px;
        }
        &__select-item {
            width: 100%;
            padding-bottom: 14px;

            .calculator-select__header {
                padding: 18px;

                &::before {
                    border: 4px solid transparent;
                    border-top: 4px solid #000;
                    right: 22px;
                    top: 25px;
                }
            }
        }
        &__main {
            padding-left: 0;
            padding-right: 0;
            padding-top: 16px;
            margin-bottom: 85px;
        }
        &__select-item-mob {
            display: flex;
            width: 100%;
            flex-direction: column;

            .calculator-select {
                width: 100%;
            }

            .calculator-select__header {
                padding: 18px;

                &::before {
                    border: 4px solid transparent;
                    border-top: 4px solid #000;
                    right: 22px;
                    top: 25px;
                }
            }
        }
        &__product-list {
            display: none;
        }
        &__option-title {
            flex-direction: column;
            padding-left: 0;
            padding-right: 0;

            h2 {
                order: 1;
                font-style: normal;
                font-weight: 500;
                font-size: 20px;
                line-height: 36px;
                margin-bottom: 20px;
            }
            img {
                position: relative;
                order: 2;
                width: 200px;
                height: 200px;
                margin-bottom: 30px;
            }
        }
        &__option-tabs {
            position: relative;
            order: 3;
        }
        &__tabs-nav {
            li {
                cursor: pointer;
                font-style: normal;
                font-weight: 600;
                font-size: 18px;
                line-height: 20px;
                padding-left: 30px;
                padding-right: 30px;

                &:first-child {
                    padding-left: 0;
                }

                &:last-child {
                    padding-right: 0;
                }
            }
        }
        &__sub-btn {
            font-style: normal;
            font-weight: 600;
            font-size: 16px;
            line-height: 18px;
            margin-top: 14px;
        }
        &__main-text {
            display: none;
        }
        &-tabs-content {
            &__inputs-wrapper {
                padding-left: 12px;
                padding-right: 12px;
            }
            &__item-wrapper {
                flex-direction: column;
                padding-left: 0;
                padding-right: 0;
            }
            &__input {
                p {
                    font-style: normal;
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 24px;
                    top: 38px;
                    right: 20px;
                }

                input {
                    padding: 14px;

                    &::placeholder {
                        font-style: normal;
                        font-weight: 500;
                        font-size: 16px;
                        line-height: 24px;
                        color: var(--main-color);
                    }
                }

                label {
                    font-style: normal;
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 24px;
                }
            }
            &__calc-column {
                max-width: none;
                padding-top: 20px;
            }
            &__item-footer {
                margin-top: 50px;
                padding-left: 12px;
                padding-right: 12px;
            }
            &__footer-block {
                flex-direction: row;
                justify-content: space-between;
                padding-top: 14px;
                padding-bottom: 14px;

                p {
                    font-style: normal;
                    font-weight: 500;
                    font-size: 20px;
                    line-height: 22px;
                }

                h3 {
                    font-style: normal;
                    font-weight: 500;
                    font-size: 34px;
                    line-height: 40px;
                }
            }
        }
    }
}
